:root {
  --color-dark: #001b2f;
  --color-light: #e9f6ff;
  --color-gray: #fafafa;
  --color-midnight: #00223a;
  --color-cyan: #00b0d7;
  --color-sunset: #ff6060;
  --color-black: #000;
  --color-white: #fff;
  --color-scroll: #686868;
  --font-main: "SF UI Display", sans-serif;
}

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
  font-size: 100%;
  vertical-align: baseline;
  background: transparent;
  box-sizing: border-box;
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
menu,
nav,
section {
  display: block;
}

:focus {
  outline: 0;
}

ol,
ul {
  list-style: none;
}

a {
  text-decoration: none;
  display: inline-block;
}

a:hover {
  text-decoration: none;
}

html,
body {
  height: 100%;
}

input,
textarea {
  border: none;
  outline: none;
  padding: 0;
  margin: 0;
}

label,
button,
input[type=submit],
input[type=button] {
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

body {
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}

img {
  max-width: 100%;
  vertical-align: middle;
  height: auto;
}

body {
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

button,
input[type=submit] {
  -webkit-appearance: none;
  -moz-appearance: none;
}

* {
  box-sizing: border-box;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

input[type=number] {
  -moz-appearance: textfield;
}

@font-face {
  font-family: "SF UI Display";
  src: url("../fonts/SFUIDisplay-Regular.ttf") format("ttf"), url("../fonts/SFUIDisplay-Regular.woff2") format("woff2"), url("../fonts/SFUIDisplay-Regular.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "SF UI Display";
  src: url("../fonts/SFUIDisplay-Medium.ttf") format("ttf"), url("../fonts/SFUIDisplay-Medium.woff2") format("woff2"), url("../fonts/SFUIDisplay-Medium.woff") format("woff");
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "SF UI Display";
  src: url("../fonts/SFUIDisplay-Semibold.ttf") format("ttf"), url("../fonts/SFUIDisplay-Semibold.woff2") format("woff2"), url("../fonts/SFUIDisplay-Semibold.woff") format("woff");
  font-weight: 600;
  font-style: normal;
  font-display: swap;
}
html::-webkit-scrollbar,
html *::-webkit-scrollbar {
  width: 6px;
  height: 6px;
}

html::-webkit-scrollbar-track,
html *::-webkit-scrollbar-track {
  background-color: rgba(0, 0, 0, 0.2);
}

html::-webkit-scrollbar-thumb,
html *::-webkit-scrollbar-thumb {
  background-color: var(--color-scroll);
}

.fp-watermark {
  display: none;
}

body {
  position: relative;
  min-width: 320px;
  margin: auto;
  background-color: var(--color-white);
  font-family: var(--font-main);
  font-weight: 400;
  color: var(--color-main);
}
body.lock {
  overflow: hidden;
}

#site {
  position: relative;
  overflow: hidden;
  min-height: 100%;
  display: flex;
  flex-direction: column;
}

.main {
  flex-grow: 1;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 5;
  background-color: var(--color-dark);
  transition: all 0.9s;
}

.cont {
  margin: 0 auto;
  max-width: 1100px;
  width: 100%;
  padding: 0 10px;
}

section {
  position: relative;
}

.calc-block {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  max-width: 320px;
  width: 100%;
  z-index: 10;
}
.calc-block__text {
  font-size: 11px;
  margin-bottom: 5px;
  color: var(--color-white);
  transition: all 0.9s;
}

.them-switcher {
  position: relative;
  width: 55px;
  height: 25px;
  background-color: var(--color-sunset);
  border-radius: 20px;
  cursor: pointer;
  margin-bottom: 10px;
  transition: all 0.9s;
}
.them-switcher span {
  position: absolute;
  width: 25px;
  height: 25px;
  border-radius: 50%;
  background-color: #093d62;
  top: 50%;
  transform: translate(0%, -50%);
  left: 0px;
  transition: all 0.9s;
}

.them-switcher.active span {
  transform: translate(120%, -50%);
  background-color: #75c0f6;
}

.main.active {
  background-color: #bee4ff;
}

.calc-block__text.active {
  color: var(--color-dark);
}

.calc {
  position: relative;
  max-width: 320px;
  width: 100%;
  border-radius: 10px 10px 20px 20px;
  padding: 5px;
  overflow: hidden;
}
.calc__btn-block {
  padding-top: 3px;
  position: relative;
}
.calc__btn-block::before {
  content: "";
  position: absolute;
  background-color: var(--color-light);
  width: calc(100% + 10px);
  height: calc(100% + 10px);
  left: -5px;
  right: 0;
  top: 0px;
  z-index: -1;
  transition: all 0.9s;
}
.calc__btns {
  display: flex;
  justify-content: space-around;
  cursor: pointer;
}
.calc__btn {
  --size: 64px;
  min-height: var(--size);
  max-width: var(--size);
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 25px;
  font-weight: 600;
  user-select: none;
  transition: all 0.9s;
}
.calc__btn:active {
  background-color: #c0e1f9;
  border-radius: 5px;
}
.calc__btn img {
  pointer-events: none;
}
.calc .func-btn {
  color: var(--color-cyan) !important;
}
.calc .clear {
  color: var(--color-sunset);
}
.calc__display {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  min-height: 171px;
  padding: 50px 0 20px;
  user-select: none;
}
.calc__display::after {
  content: "";
  position: absolute;
  background-color: var(--color-gray);
  width: calc(100% + 10px);
  height: calc(100% + 20px);
  left: -5px;
  top: -5px;
  z-index: -1;
  transition: all 0.9s;
}
.calc__area {
  font-weight: 400;
  font-size: 24px;
  line-height: 20px;
  letter-spacing: -0.24px;
  transition: all 0.9s;
}
.calc__area strong {
  color: var(--color-sunset) !important;
  margin: 0 3px;
}
.calc__total {
  margin-top: 16px;
  font-weight: 600;
  font-size: 32px;
  word-wrap: break-word;
  word-break: break-all;
  line-height: 1.1;
  transition: all 0.9s;
}

.th-b .calc__display::after {
  transition: all 0.9s;
  background-color: var(--color-midnight);
}
.th-b .calc__area {
  transition: all 0.9s;
  color: var(--color-white);
}
.th-b .calc__total {
  transition: all 0.9s;
  color: var(--color-white);
}
.th-b .calc__btn-block::before {
  transition: all 0.9s;
  background-color: var(--color-dark);
}
.th-b .calc__btn {
  color: var(--color-white);
  transition: all 0.9s;
}
.th-b .calc__btn:active {
  background-color: #093d62;
}

.btn {
  position: relative;
  display: flex;
  align-items: center;
  max-width: 180px;
  width: fit-content;
  font-weight: 500;
  font-size: 17px;
  line-height: 20px;
  padding: 10px 34px;
  background: linear-gradient(180deg, #ff680a 0%, #fe8c44 100%);
  border-radius: 6px;
  border: none;
  color: #fff;
  cursor: pointer;
  overflow: hidden;
  letter-spacing: 0.1px;
}
.btn span {
  z-index: 5;
}
.btn::before {
  content: "";
  position: absolute;
  background: linear-gradient(180deg, #ff9c5e 0%, #ff680a 100%);
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  opacity: 0;
  transition: all 0.4s ease-in-out;
}
.btn:hover::before {
  opacity: 1;
  transition: all 0.4s ease-in-out;
}

input,
textarea {
  position: relative;
  width: 100%;
  resize: vertical;
  background-color: transparent;
  border: 1px solid rgba(0, 0, 0, 0.1);
  border-radius: 3px;
  padding: 13px 3px 13px 13px;
  transition: 0.3s ease-in-out all;
  font-family: var(--font-main);
  font-weight: 400;
  font-size: 17px;
  line-height: 20px;
  color: var(--color-main);
  margin: 4px 0px 16px;
}
input::placeholder,
textarea::placeholder {
  font-weight: 400;
  font-size: 17px;
  line-height: 20px;
  color: rgba(0, 0, 0, 0.26);
}
input:focus,
textarea:focus {
  border-color: var(--color-citrus);
}
input:-webkit-autofill, input:-webkit-autofill:hover, input:-webkit-autofill:focus, input:-webkit-autofill:active,
textarea:-webkit-autofill,
textarea:-webkit-autofill:hover,
textarea:-webkit-autofill:focus,
textarea:-webkit-autofill:active {
  transition: background-color 5000s ease-in-out 0s;
  -webkit-text-fill-color: #01013d !important;
}

textarea {
  height: 100px;
}

@keyframes jelly {
  from {
    transform: scale(1, 1);
  }
  30% {
    transform: scale(1.25, 0.75);
  }
  40% {
    transform: scale(0.75, 1.25);
  }
  50% {
    transform: scale(1.15, 0.85);
  }
  65% {
    transform: scale(0.95, 1.05);
  }
  75% {
    transform: scale(1.05, 0.95);
  }
  to {
    transform: scale(1, 1);
  }
}
@media only screen and (max-width: 551px) {
  .main-form {
    top: 0px;
    overflow: auto;
    max-width: 100vw;
    height: 100vh;
  }
}
@media only screen and (max-width: 480px) {
  .cont {
    padding: 0 20px;
  }
}