@font-face {
	font-family: "SF UI Display";
	src: url("../fonts/SFUIDisplay-Regular.ttf") format("ttf"),
		url("../fonts/SFUIDisplay-Regular.woff2") format("woff2"),
		url("../fonts/SFUIDisplay-Regular.woff") format("woff");
	font-weight: normal;
	font-style: normal;
	font-display: swap;
}

@font-face {
	font-family: "SF UI Display";
	src: url("../fonts/SFUIDisplay-Medium.ttf") format("ttf"),
		url("../fonts/SFUIDisplay-Medium.woff2") format("woff2"),
		url("../fonts/SFUIDisplay-Medium.woff") format("woff");
	font-weight: 500;
	font-style: normal;
	font-display: swap;
}

@font-face {
	font-family: "SF UI Display";
	src: url("../fonts/SFUIDisplay-Semibold.ttf") format("ttf"),
		url("../fonts/SFUIDisplay-Semibold.woff2") format("woff2"),
		url("../fonts/SFUIDisplay-Semibold.woff") format("woff");
	font-weight: 600;
	font-style: normal;
	font-display: swap;
}
